@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&family=Roboto+Condensed:wght@700&display=swap);

.start-game-screen{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 1;
    grid-gap: 1.6rem;
    gap: 1.6rem;
    width:100%;
}
.best-score{
    font-family: 'Fredoka One', cursive; 
    color:white;
    font-size: 3.2rem;
    text-align: center;
    width:80%;
    z-index: 1;
    text-shadow: 0.4rem 0.4rem rgb(0,0,0,0.5);
}
.start-game-desc{
    font-family: 'Fredoka One', cursive; 
    color:white;
    font-size: 1.8rem;
    text-align: center;
    width:80%;
    z-index: 1;
}
.start-game-btn{
    font-family: 'Fredoka One', cursive; 
    color:white;

    padding: 0.8rem;
    padding-left: 1.6rem;
    padding-right: 1.6rem;
    border-radius: 9999px;
    font-size: 1.8rem;
    background-color: transparent;

    border: solid 0.4rem black;
    transition: all 0.3s;
}
.start-game-btn:hover{
    background-color: black;
}

.play-game-screen{
    height: 100%;
    width: 90%;
    z-index: 1;
    display:flex;
    flex-direction: column;
    grid-gap: 0.8rem;
    gap: 0.8rem;
}
.current-score{
    font-family: 'Fredoka One', cursive;
    font-size: 2.4rem;
    color:white;

    height: 8%;
    display:flex;
    justify-content: center;
    align-items: center;
    text-shadow: 0.3rem 0.3rem rgb(0,0,0,0.5);

}
.memory-card-grid{
    overflow: auto;
    display: grid;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(10, 1fr);
    height: 90%;

}
.memory-card{
    height: 60px;
    font-family: 'Fredoka One', cursive;
    font-size: 1.4rem;

    border-radius: 9999px;
    background-color: transparent;
    color:white;
    background-color: rgb(0, 0, 0, 0.5);
    border: none;
    transition: all 0.1s;
}
.memory-card:hover{
    background-color: black;
}

::-webkit-scrollbar {
    width: 0;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: transparent;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: transparent;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: transparent;
  }

  @media only screen and (max-width: 600px) {
    .memory-card-grid{
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(25, 1fr);
   
    }
    .memory-card:hover{
        background-color: rgb(0, 0, 0, 0.5);
    }

  }
html,body{
    margin: 0;
    height:100%;
    font-size: 15px;
}

#header{
    font-family: 'Fredoka One', cursive;
    font-size: 1.8rem;
    color: white;
    height: 8vh;

    display:flex;
    align-items: center;
    
    padding-left: 1em;
    background-color:black;
    z-index: 1;
}
#main{
    background-image: linear-gradient(orange,blue);
    height: 90vh;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 1;
}
#footer{
    font-family: 'Fredoka One', cursive;  
    font-size: 0.8rem;
    height: 2vh;

    padding-left: 1em;
    background-color: black;
    z-index: 1;
}
#footer>a{
    text-decoration: none;
    color:white;
}

.shapes{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height:0;
    width: 0;
    
    
}
.shapes li {
    position:absolute;
	list-style: none;
	width: 30px;
	height: 30px;
    border-radius: 50%; 
	background: orange;
    border: none;
	-webkit-animation: animate 10s linear infinite;
	        animation: animate 10s linear infinite;
	bottom: 0;

    overflow: hidden;
}
@-webkit-keyframes animate {
	0% {
        -webkit-transform: translateY(0) translateX(0);
                transform: translateY(0) translateX(0);
		opacity: 0;
	}
	50% {
        -webkit-transform: translateY(2vh) translateX(2vw);
                transform: translateY(2vh) translateX(2vw);
		opacity: 0.5;
	}
	100% {
        -webkit-transform: translateY(4vh) translateX(4vw);
                transform: translateY(4vh) translateX(4vw);
		opacity: 0;
	}
}
@keyframes animate {
	0% {
        -webkit-transform: translateY(0) translateX(0);
                transform: translateY(0) translateX(0);
		opacity: 0;
	}
	50% {
        -webkit-transform: translateY(2vh) translateX(2vw);
                transform: translateY(2vh) translateX(2vw);
		opacity: 0.5;
	}
	100% {
        -webkit-transform: translateY(4vh) translateX(4vw);
                transform: translateY(4vh) translateX(4vw);
		opacity: 0;
	}
}
